/* Image css */

.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px rgb(110, 108, 108);
            box-shadow: 0 3px 6px #999, 0 3px 6px rgb(110, 108, 108);
    text-align: left;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
    background:white;
  }

  .image {
    width: 100%;
    height: auto;
  }


